import React, { useState } from 'react';
import PropTypes from 'prop-types';
import styles from './index.css';
import errorBoundary from '@src/components/errorBoundary';
import { FormStore } from '../../store';
import { Core } from '@ifeng/web_upload_core';
import { chooseImage, chooseVideo } from '@src/public/utils/media';
import Grid from './Grid';

const imgLimit = 9;
const videoLimit = 2;

const Upload = () => {
    const formStore = FormStore.useContainer();
    const { formStates, changeFormStates } = formStore;
    const { imageList, videoList } = formStates;

    const handleImgUploadClick = async () => {
        const list = await chooseImage({ count: imgLimit - imageList.length });

        changeFormStates({ imageList: [...imageList, ...list] });
    };

    const handleVideoUploadClick = async () => {
        const list = await chooseVideo({ count: videoLimit - videoList.length });

        changeFormStates({ videoList: [...videoList, ...list] });
    };

    return (
        <div className={styles.wrap}>
            {imageList.map(item => {
                const { id } = item;

                return <Grid content={item} key={id} type={'image'} />;
            })}
            {imageList.length >= imgLimit || videoList.length ? null : (
                <div className={styles.imgUploadBtn} onClick={handleImgUploadClick}>
                    添加图片
                </div>
            )}
            {videoList.map(item => {
                const { id } = item;

                return <Grid content={item} key={id} type={'video'} />;
            })}
            {videoList.length >= videoLimit || imageList.length ? null : (
                <div className={styles.imgUploadBtn} onClick={handleVideoUploadClick}>
                    添加视频
                </div>
            )}
        </div>
    );
};

export default errorBoundary(Upload);
