import { useState } from 'react';
import { createContainer } from 'unstated-next';

// 表单相关的状态
const useFormStore = (
    initialState = {
        content: '',
        imageList: [],
        videoList: [],
        audioList: [],
        mobile: '',
        verifyCode: '',
        uploadMap: new Map(),
        captchaRequired: false,
    },
) => {
    const [formStates, setFormStates] = useState(initialState);

    const changeFormStates = newFormStates => {
        setFormStates(prevStates => Object.assign({}, prevStates, newFormStates));
    };

    return { formStates, changeFormStates };
};

export const FormStore = createContainer(useFormStore);
