import React, { useState } from 'react';
import PropTypes from 'prop-types';
import styles from './index.css';
import errorBoundary from '@src/components/errorBoundary';
import { FormStore } from '../../store';
import axios from '@src/public/utils/request';
import { jsonp } from '@ifeng/ui_base';

const Verify = () => {
    const formStore = FormStore.useContainer();
    const { formStates, changeFormStates } = formStore;
    const { mobile, verifyCode, captchaRequired } = formStates;

    const handleMobileChange = e => {
        changeFormStates({ mobile: e.target.value });
    };

    const handleVerifyCodeChange = e => {
        changeFormStates({ verifyCode: e.target.value });
    };

    const handleSendsmsClick = async () => {
        const result = await jsonp(
            `https://user.ifeng.com/api/v1/sendsms?params=${encodeURIComponent(
                JSON.stringify({ mobile, smsform: 1 }),
            )}`,
            {
                jsonpCallback: 'sendsmsCallback',
            },
        );
    };

    return (
        <div>
            <div>联系方式</div>
            <div>
                <input type="text" value={mobile} onChange={handleMobileChange} />
            </div>
            <div>验证码</div>
            <div>
                <input type="text" value={verifyCode} onChange={handleVerifyCodeChange} />
                <button onClick={handleSendsmsClick}>获取验证码</button>
            </div>
            {captchaRequired ? (
                <div>
                    <img src="" alt="" />
                    <input type="text" />
                </div>
            ) : null}
        </div>
    );
};

export default errorBoundary(Verify);
