import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import styles from './index.css';
import errorBoundary from '@src/components/errorBoundary';
import { FormStore } from '../../../store';
import { Core } from '@ifeng/web_upload_core';

const Grid = ({ content, type }) => {
    const { path, id, file } = content;
    const [state, setState] = useState('上传中');
    const formStore = FormStore.useContainer();
    const { formStates } = formStore;
    const { uploadMap } = formStates;

    useEffect(() => {
        const cos = new Core({
            appid: 'ifeng-timeline',
            token: '2687BFBFD337D0F27090FAFBF2171EF2',
            apiUrl: 'https://skserver.ifeng.com/cos/getCredential',
        });

        if (file && file.size) {
            cos.uploadBigFile({
                file,
                success: res => {
                    const { sourcePath } = res;

                    uploadMap.set(id, sourcePath);
                    setState('上传成功');
                    console.log(uploadMap);
                },
                error: err => {
                    console.err(err);
                    setState('上传失败');
                },
            });
        }
    }, []);

    return (
        <div className={styles.wrap}>
            <div className={styles.state}>{state}</div>
            {type === 'image' ? <img src={path} alt="" className={styles.thumbnail} /> : null}
            {type === 'video' ? <video src={path} className={styles.video} controls /> : null}
        </div>
    );
};

Grid.propTypes = {
    content: PropTypes.object,
    type: PropTypes.string,
};

export default errorBoundary(Grid);
