import React from 'react';
import PropTypes from 'prop-types';
import errorBoundary from '@src/components/errorBoundary';
import { useMaximize } from '@src/public/hooks/app';
import { FormStore } from '../store';
import { exposures } from '../service';
import styles from './index.css';

const Nav = ({ id }) => {
    const formStore = FormStore.useContainer();
    const { formStates, changeFormStates } = formStore;
    const { content, imageList, videoList, audioList, mobile, verifyCode, uploadMap, captchaRequired } = formStates;

    const statusBarHeight = useMaximize();

    const isAllowSubmit = () => {
        return content && mobile && verifyCode;
    };

    const handleSubmit = () => {
        const timelineId = id;

        const medias = [
            ...imageList.map(item => {
                const { id } = item;
                const url = uploadMap.get(id);

                return { url, type: 'img' };
            }),
            ...videoList.map(item => {
                const { id } = item;
                const url = uploadMap.get(id);

                return { url, type: 'video' };
            }),
        ];

        if (isAllowSubmit()) {
            exposures(timelineId, content, medias, mobile, verifyCode).then(() => {
                // 提交成功
            });
        }
    };

    return (
        <div className={styles.wrap} style={{ paddingTop: statusBarHeight }}>
            <div className={styles.content}>
                <div className={styles.back}>取消</div>
                <div className={styles.title}>爆料页</div>
                <div className={`${styles.submit} ${!isAllowSubmit() ? styles.notAllow : ''}`} onClick={handleSubmit}>
                    提交
                </div>
            </div>
            <div className={styles.info}>爆料一经采用并发布，将根据内容给予相应的线索奖励</div>
        </div>
    );
};

Nav.propTypes = {
    id: PropTypes.string,
};

export default errorBoundary(Nav);
