import { v4 as uuidv4 } from 'uuid';

// 封装调起相册选择图片API
export const chooseImage = async ({ count = 1 }) => {
    const imageId = 'chooseImage';
    // 获取上传文件的dom节点
    let chooseImageId = document.getElementById(imageId);

    // 如果上传文件的dom节点不存在则创建
    if (!chooseImageId) {
        const obj = document.createElement('input');

        obj.setAttribute('type', 'file');
        obj.setAttribute('id', imageId);
        if (count > 1) {
            obj.setAttribute('multiple', 'multiple');
        }
        obj.setAttribute('accept', 'image/*');
        obj.setAttribute('style', 'position: fixed; top: -4000px; left: -3000px; z-index: -300;');
        document.body.appendChild(obj);
        chooseImageId = document.getElementById(imageId);
    } else if (count > 1) {
        chooseImageId.setAttribute('multiple', 'multiple');
    } else {
        chooseImageId.removeAttribute('multiple');
    }

    // 创建鼠标点击事件
    const event = new MouseEvent('click');

    // 获取上传的文件列表
    const files = await new Promise(resolve => {
        chooseImageId.onchange = e => {
            resolve([...e.target.files]);
        };
        // 上传文件dom触发鼠标点击事件
        chooseImageId.dispatchEvent(event);
    });

    // 使用FileReader获取base64链接
    const list = await Promise.all(
        files.slice(0, count).map(async file => {
            const url = await new Promise(resolve => {
                const fileReader = new FileReader();

                fileReader.onload = e => {
                    resolve(e.target.result);
                };
                fileReader.readAsDataURL(file);
            });

            return { path: url, size: file.size, type: file.type, file, id: uuidv4() };
        }),
    );

    return list;
};

// 封装调起相册选择视频API
export const chooseVideo = async ({ count = 1 }) => {
    const videoId = 'chooseVideo';
    // 获取上传文件的dom节点
    let chooseVideoId = document.getElementById(videoId);

    // 如果上传文件的dom节点不存在则创建
    if (!chooseVideoId) {
        const obj = document.createElement('input');

        obj.setAttribute('type', 'file');
        obj.setAttribute('id', videoId);
        if (count > 1) {
            obj.setAttribute('multiple', 'multiple');
        }
        obj.setAttribute('accept', 'video/*');
        obj.setAttribute('style', 'position: fixed; top: -4000px; left: -3000px; z-index: -300;');
        document.body.appendChild(obj);
        chooseVideoId = document.getElementById(videoId);
    } else if (count > 1) {
        chooseVideoId.setAttribute('multiple', 'multiple');
    } else {
        chooseVideoId.removeAttribute('multiple');
    }

    // 创建鼠标点击事件
    const event = new MouseEvent('click');

    // 获取上传的视频
    const files = await new Promise(resolve => {
        chooseVideoId.onchange = e => {
            resolve([...e.target.files]);
        };
        // 上传文件dom触发鼠标点击事件
        chooseVideoId.dispatchEvent(event);
    });

    const list = files.slice(0, count).map(file => {
        const blob = new Blob([file], { type: 'video/mp4' });
        const url = URL.createObjectURL(blob);

        return { path: url, size: file.size, type: file.type, file, id: uuidv4() };
    });

    return list;
};

export const image2Base64 = (img, callback) => {
    const newImg = new Image();

    newImg.setAttribute('crossOrigin', 'anonymous');
    newImg.onload = () => {
        const canvas = document.createElement('canvas');

        canvas.width = Math.min(newImg.width, 300);
        canvas.height = newImg.width > 300 ? (300 / newImg.width) * newImg.height : newImg.height;
        const ctx = canvas.getContext('2d');

        ctx.drawImage(newImg, 0, 0, canvas.width, canvas.height);
        const dataURL = canvas.toDataURL('image/png');

        callback(dataURL);
    };
    newImg.src = img;
};
