import React from 'react';
import errorBoundary from '@src/components/errorBoundary';
import { FormStore } from '../../store';
import styles from './index.css';

const limitLength = 1000;

const Text = () => {
    const formStore = FormStore.useContainer();
    const { formStates, changeFormStates } = formStore;
    const { content } = formStates;
    const limit = 1000;

    const handleTextareaChange = e => {
        changeFormStates({ content: e.target.value });
    };

    return (
        <div className={styles.wrap}>
            <div className={styles.textarea}>
                <textarea value={content} onChange={handleTextareaChange} />
                {!content ? (
                    <div className={styles.placeholder}>
                        <span className={styles.require}>*</span>
                        爆料详情（必填，如：时间、地点、人物）
                    </div>
                ) : null}
            </div>

            <div className={`${styles.limit} ${content.length >= 1000 ? styles.overLimit : ''}`}>
                (
                <span className={content.length >= limitLength ? styles.overLimit : ''}>{content.length}</span>
                /{limit})
            </div>
        </div>
    );
};

export default errorBoundary(Text);
